.App {
  text-align: center;
  
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.Position-fixed {
  position: fixed;
  width: 100%;
  height: 200px;
  padding-top: 10px;
}

.padding-left {
  padding-left: 15px;
  padding-top: 10px;
}

.width-left {
  width: 341px;
  height: 250px;
}

.button-left {
  position: fixed !important;
  left: 10px;
}

.button-right {
  position: fixed !important;
  right: 10px;
}


